/*
 * ensure that parents of page elements have full height:
 * html, body
 */
html {
	margin:0;
	padding:0;
	height:100%;
}

.scrollable {
  overflow-y: auto;
  max-height: 240px;
}

body {
	margin:0;
	padding:0;
	height:100%;
}
/* p5.js style addition */
canvas {
  display: block;
}

li li{
    list-style-type: lower-alpha;
}

li li li{
    list-style-type: lower-roman;
}

.C {
  background-color: rgba(95,158,160,0.75);
}
.M {
	background-color: rgba(128,64,0,0.75);
}
.O {
	background-color: rgba(128,128,0,0.75);
}
.B {
	background-color: rgba(245,245,220,0.75);
}
/*
 * suggestion from https://stackoverflow.com/questions/34092774/bootstrap-navbar-collapse-dropdown-menu
 * to fix ios nav collapse button data-toggle
 */
.navbar-static-top .navbar-collapse.in .navbar-expand .navbar-expand-sm {
    overflow-y: visible !important; /* Bootstrap default is "auto" */
}
/*
 * style for home page
 */
.splash {
	background-image: url('/~hepting/assets/bg/home.jpg');
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	color: white;
	text-align: center;
	text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);
}

